<template>
    <nav class="navbar">
        <span class="open-slide">
            <a v-on:click="openSideMenu">
                <svg width="30" height="30">
                    <path d="M0,5 30,5" stroke="#fff" stroke-width="5"/>
                    <path d="M0,14 30,14" stroke="#fff" stroke-width="5"/>
                    <path d="M0,23 30,23" stroke="#fff" stroke-width="5"/>                    
                </svg>
            </a>
        </span>
        <span class="logo">CnodeJS</span>
        <ul class="navbar-nav">
            <li><a href="#">Home</a></li>
            <li><a href="#">Share</a></li>
            <li><a href="#">Ask</a></li>
            <li><a href="#">About</a></li>            
        </ul>
    </nav>
</template>
<script>
export default {
    methods : {
        openSideMenu : function() {
            document.getElementById('side-menu').style.width
             = '250px';  
        }
    }
  
}
</script>
<style scoped>
.navbar {
  background-color: #3b5998;
  overflow: hidden;
  height: 63px;
}

.navbar a {
  float: left;
  display: block;
  color: #f2f2f2;
  text-align: center;
  padding: 10px 16px;
  text-decoration: none;
  font-size: 20px;
}
.open-slide a{
    float: left;
    display: block;
    color: #f2f2f2;
    text-align: center;
    padding: 14px 16px;
    text-decoration: none;
    font-size: 20px;
}
.logo {
    float: left;
    display: block;
    color: #ccc;
    text-align: center;
    padding: 18px 16px;
    font-size: 20px;
    font-weight: bold;
}

.navbar ul {
  list-style: none;
  margin: 8px 0 0 0;
}

.navbar a:hover {
  background-color: #ddd;
  color: #000;
}


</style>



