<template>
    <div class="main">
        <div v-html="cont" class="art-main"></div>
    </div>
</template>

<script>
import marked from 'marked'
export default {
    data() {
        return {
            id: this.$route.params.id,
            article: [],
            cont:''
        }
    },
    methods: {
        getData() {
            
            this.$http.get(
            'https://cnodejs.org/api/v1/topic/'+this.id,
            {
                params: {
                mdrender: 'false'
                }
            }  
            ).then((res) => {
            this.article = res.data.data;
            this.cont = marked(this.article.content)
            },(res) => {console.log('singleSection:',res)});   
        }
    },
    created() {
        this.getData()
    }
}
</script>

<style>
    a {
        text-decoration: none;
        color: #999;
    }
    li {
        list-style: none;
    }
    .main {
        max-width: 1200px;
        width: 90%;
        border: 1px solid #666;
        background-color: #eee;
        margin: 20px auto;
        border-radius: 10px;
        padding: 30px;
    }
    .art-main img {
        width: 100%;
        height: 100%;
    }
</style>



