import Vue from 'vue'
import Router from 'vue-router'
import mainsec from '../components/MainSection'
import article from '../components/article'


Vue.use(Router)

export default new Router({
  routes: [
    {path: '/', component: mainsec},
    {path: '/article/:id', component: article}
  ]
})



