<template>
    <div id="side-menu" class="side-nav">
        <a class="btn-close" v-on:click="closeSideMenu">&times;</a>
        <a href="#" v-on:click="closeSideMenu">Home</a>
        <a href="#" v-on:click="closeSideMenu">Lab</a>
        <a href="#" v-on:click="closeSideMenu">Blog</a>
        <a href="#" v-on:click="closeSideMenu">About</a>        
    </div>
</template>

<script>
export default {
    methods : {
        closeSideMenu : function() {
            document.getElementById('side-menu').style.width
             = '0';  
        }
    }
}
</script>
<style scoped>
.side-nav {
    height: 100%;
    width: 0;
    position: fixed;
    z-index: 1;
    top: 0;
    left: 0;
    background-color: #111;
    opacity: .9;
    overflow-x: hidden;
    padding-top: 60px;
    transition: .5s;
}
.side-nav a{
    padding: 10px 10px 10px 30px;
    text-decoration: none;
    font-size: 22px;
    color: #ccc;
    display: block;
    transition: .3s;
}
.side-nav a:hover {
    color: #fff;
}
.side-nav .btn-close {
    position: absolute;
    top: 0;
    right: 22px;
    font-size:36px;
    margin-left:50px;
    cursor: pointer;
}
</style>



