<template>
    <router-link :to="id">
        <article class="art">
            <div class="art-head">
                <span class="author-img"><img :src="img"></span>
                <div class="author-name">作者：{{author}}</div>
                <div class="pub-time">发布于：{{getDate(pubtime)}}</div>
            </div>
            <div class="art-title">{{title}}</div>
        </article>
    </router-link>
</template>

<script>
export default {
    data() {
        return {
            img: this.msg.author.avatar_url,
            author: this.msg.author.loginname,
            pubtime: this.msg.create_at,
            title: this.msg.title,
            id: "/article/"+this.msg.id
        }
    },
    props:['msg'],
    methods: {
        getDate: function(date) {
            let now = new Date()
            let i = 1
            if(now.getFullYear() == date.substr(0, 4)) {
                if((now.getMonth()+1) == date.substr(5, 2)) {
                    if(now.getDate() == date.substr(8, 2)) {
                        if(now.getHours() == date.substr(11, 2)) {
                            if(now.getMinutes() == date.substr(14, 2)) {
                                return "刚刚"
                            }else{
                                return (now.getMinutes()-date.substr(14, 2)) + "分钟前"
                            }
                        }else{
                            return (now.getHours() - date.substr(11, 2)) + "小时前"
                        }
                    }else{
                        return (now.getDate() - date.substr(8, 2)) + "天前"
                    }
                }else{
                    return ((now.getMonth()+1) - date.substr(5, 2)) + "个月前"
                }
            }else{
                return (now.getFullYear() - date.substr(0, 4)) + "年前"
            }
        }
    },
    created() {
        console.log(this.msg.tab)
    }
}
</script>

<style scoped>
a {
    text-decoration: none;
}
.art {
    background-color: #fff;
    box-shadow: 1px 2px 1px #666;
    margin: 20px 20px;
}
.art-head {
    height: 66px;
    border-bottom: 1px solid #999;
}
.author-img img{
    width: 40px;

}
.author-img {
    margin: 10px;
    float: left;
}

.author-name {
    float: left;
    padding: 20px 10px;
    font-size: 20px;
    color: #666;
}
.pub-time {
    float: left;
    padding: 20px 10px;
    font-size: 20px;
    color: #666;
}

.art-title {
    font-size: 24px;
    padding: 20px;  
    color: #333; 
}

@media (max-width:567px) {
    .pub-time {font-size: 14px;}
    .author-name {font-size: 14px;}
    .art-title {font-size: 18px;}
}

</style>



