<template>
  <div>
    <div v-for='item of content' :key='item.id'>
      <singlesec :msg=item ></singlesec>
    </div>
  </div> 
</template>

<script>
import singlesec from './singleSec'
export default {
  name:'MainSection',
  components:{
    singlesec
  },
  data() {
    return {
      content: []
    }
  },
  created() {
    this.getData()
  },
  methods: {
    getData() {
      this.$http.get(
        'https://cnodejs.org/api/v1/topics',
        {
          params: {
          page: 1,
          limit: 20,
          mdrender: 'false'
          }
        }  
      ).then((res) => {
        this.content = res.data.data;
        console.log(this.content)
      },(res) => {console.log('MainSection:',res)});   
    }
  }
}
</script>

<style scoped>

</style>



